import * as React from "react";
import Grid from "@mui/material/Grid";
import DehazeIcon from "@mui/icons-material/Dehaze";
import { Button } from "@mui/material";
import { Link } from "react-router-dom";

export default class GameScreen2 extends React.Component {
  constructor(props) {
    super(props);
    this.state = { isMenu: false, isDesicion: false };
  }
  componentDidMount() {
    setTimeout(() => {
      this.setState({ isDesicion: true });
    }, 3000);
  }
  menuClicked = () => {
    this.setState({ isMenu: !this.state.isMenu });
  };
  render() {
    const { isMenu, isDesicion } = this.state;
    return (
      <Grid
        container
        style={{
          backgroundColor: "white",
          height: "100vh",
          alignContent: "baseline",
        }}
      >
        <Grid
          container
          style={{
            backgroundColor: "#8AA29E",
            height: "10vh",
            alignItems: "center",
          }}
        >
          <Grid
            item
            xs={10}
            style={{
              justifyContent: "center",
              display: "flex",
              color: "#fff",
              fontSize: "20px",
              fontWeight: 600,
            }}
          >
            Anti smoke game
          </Grid>
          <Grid
            item
            xs={2}
            onClick={this.menuClicked}
            style={{ display: "flex", justifyContent: "center" }}
          >
            <DehazeIcon
              style={{ color: "white", fontSize: "2rem", cursor: "pointer" }}
            />
          </Grid>
        </Grid>
        <Grid
          container
          style={{
            backgroundColor: "#8AA29E",
            height: isMenu ? "56%" : "0%",
            transition: "height 1.2s",
          }}
        >
          <div
            container
            style={{
              height: "100%",
              backgroundColor: "#8AA29E",
              alignItems: "center",
              transition: "height 1.2s",
              color: "#fff",
              fontSize: "20px",
              fontWeight: 600,
              flexDirection: "column",
              width: "100%",
            }}
          >
            <div
              className="menuStyle"
              style={{ display: isMenu ? "block" : "none" }}
            >
              <Link
                to="/Smoke-Away/homescreen"
                style={{ textDecoration: "none", color: "#fff" }}
              >
                Home
              </Link>
            </div>
            <div
              className="menuStyle"
              style={{ display: isMenu ? "block" : "none" }}
            >
              My progress
            </div>
            <div
              className="menuStyle"
              style={{ display: isMenu ? "block" : "none" }}
            >
              Informations
            </div>
            <div
              className="menuStyle"
              style={{ display: isMenu ? "block" : "none" }}
            >
              Check for the symptomss
            </div>
            <div
              className="menuStyle"
              style={{ display: isMenu ? "block" : "none" }}
            >
              Health tracking
            </div>
            <div
              className="menuStyle"
              style={{ display: isMenu ? "block" : "none" }}
            >
              Stress
            </div>
            <div
              className="menuStyle"
              style={{ display: isMenu ? "block" : "none" }}
            >
              Account
            </div>
          </div>
        </Grid>
        <Grid
          container
          style={{
            alignItems: "center",
            color: "#686963",
            height: "20vh",
            alignContent: "center",
            marginTop: "3vh",
          }}
        >
          <Grid
            container
            style={{
              justifyContent: "center",
              color: "#686963",
              height: "90%",
            }}
          >
            <div
              style={{
                width: "65%",
                height: "55%",
                border: "3px solid #E5E5E5",
                borderRadius: "16px",
                fontSize: "100%",
                padding: "30px",
                display: "flex",
                justifyContent: "center",
                textAlign: "center",
                alignItems: "center",
              }}
            >
              Narrator: You are at the bus stop, waiting for a bus to arrive. Do
              you wanna take a smoke?
            </div>
          </Grid>
        </Grid>
        <Grid
          container
          style={{
            alignItems: "center",
            height: "65vh",
            marginTop: "5vh",
            alignContent: "center",
          }}
        >
          <img
            src={require("./Assets/game2.png")}
            alt="not found"
            style={{ width: "100%", height: "100%" }}
          />
        </Grid>
        <Grid
          md={4}
          style={{
            height: isDesicion ? "100%" : "0%",
            backgroundColor: "#1414148c",
            position: "absolute",
            width: "100%",
            transition: "height 2s",
          }}
        >
          <Grid
            container
            style={{
              display: isDesicion ? "flex" : "none",
              justifyContent: "center",
              marginTop: "35vh",
            }}
          >
            <Grid
              item
              xs={5}
              style={{ display: "flex", justifyContent: "center" }}
            >
              <Link
                to="/Smoke-Away/gameScreen4"
                style={{ textDecoration: "none", width: "90%", color: "#fff" }}
              >
                <Button
                  variant="contained"
                  style={{
                    color: "white",
                    backgroundColor: "#DB5461",
                    width: "100%",
                    height: "60px",
                  }}
                >
                  Yes
                </Button>
              </Link>
            </Grid>
            <Grid
              item
              xs={5}
              style={{ display: "flex", justifyContent: "center" }}
            >
              <Link
                to="/Smoke-Away/gameScreen3"
                style={{ textDecoration: "none", width: "90%", color: "#fff" }}
              >
                <Button
                  variant="contained"
                  style={{
                    color: "white",
                    backgroundColor: "#8FDB54",
                    width: "100%",
                    height: "60px",
                  }}
                >
                  No
                </Button>
              </Link>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    );
  }
}
