import * as React from "react";
import Grid from "@mui/material/Grid";
import DehazeIcon from "@mui/icons-material/Dehaze";
import { Link } from "react-router-dom";
export default class HomeScreen extends React.Component {
  constructor(props) {
    super(props);
    this.state = { isMenu: false };
  }
  menuClicked = () => {
    this.setState({ isMenu: !this.state.isMenu });
  };
  render() {
    const { isMenu } = this.state;
    return (
      <Grid
        container
        style={{
          backgroundColor: "#8AA29E",
          height: "100vh",
        }}
      >
        <Grid
          container
          style={{
            backgroundColor: "#8AA29E",
            height: "10vh",
            alignItems: "center",
          }}
        >
          <Grid
            item
            xs={10}
            style={{
              justifyContent: "center",
              display: "flex",
              color: "#fff",
              fontSize: "20px",
              fontWeight: 600,
            }}
          >
            Home
          </Grid>
          <Grid
            item
            xs={2}
            onClick={this.menuClicked}
            style={{ display: "flex", justifyContent: "center" }}
          >
            <DehazeIcon
              style={{ color: "white", fontSize: "2rem", cursor: "pointer" }}
            />
          </Grid>
        </Grid>
        <div
          container
          style={{
            height: isMenu ? "56%" : "0%",
            backgroundColor: "#8AA29E",
            alignItems: "center",
            transition: "height 1.2s",
            color: "#fff",
            fontSize: "20px",
            fontWeight: 600,
            flexDirection: "column",
            width: "100%",
          }}
        >
          <div
            className="menuStyle"
            style={{ display: isMenu ? "block" : "none" }}
          >
            My progress
          </div>
          <div
            className="menuStyle"
            style={{ display: isMenu ? "block" : "none" }}
          >
            Informations
          </div>
          <div
            className="menuStyle"
            style={{ display: isMenu ? "block" : "none" }}
          >
            Check for the symptomss
          </div>
          <div
            className="menuStyle"
            style={{ display: isMenu ? "block" : "none" }}
          >
            Health tracking
          </div>
          <div
            className="menuStyle"
            style={{ display: isMenu ? "block" : "none" }}
          >
            Stress
          </div>
          <div
            className="menuStyle"
            style={{ display: isMenu ? "block" : "none" }}
          >
            Account
          </div>
          <div
            className="menuStyle"
            style={{ display: isMenu ? "block" : "none" }}
          >
            <Link
              to="/Smoke-Away/gameScreen1"
              style={{ textDecoration: "none", color: "#fff" }}
            >
              Anti smoke game
            </Link>
          </div>
        </div>

        <Grid
          container
          style={{
            alignItems: "center",
          }}
        >
          <img
            src={require("./Assets/lungcancer.png")}
            alt="not found"
            style={{ width: "100%", height: "35vh" }}
          />
        </Grid>

        <Grid
          container
          style={{
            alignItems: "center",
            backgroundColor: "#fff",
          }}
        >
          <Grid
            container
            style={{
              color: "#686963",
              fontSize: "30px",
              fontWeight: 600,
              marginTop: "4vh",
              justifyContent: "center",
            }}
          >
            Lung Cancer
          </Grid>
          <Grid
            container
            style={{
              color: "#686963",
              fontSize: "20px",
              padding: "3vh",
              textAlign: "center",
            }}
          >
            Lung cancer is the uncontrolled growth of abnormal cells in one or
            both lungs. These abnormal cells do not carry out the functions of
            normal lung cells, and they form tumours as they grow. These tumours
            interfere with the lungs function of providing oxygen to the body
            via the blood.
          </Grid>
        </Grid>

        <Grid
          container
          style={{
            alignItems: "center",
          }}
        >
          <img
            src={require("./Assets/cause.png")}
            alt="not found"
            style={{ width: "100%", height: "35vh" }}
          />
        </Grid>

        <Grid
          container
          style={{
            alignItems: "center",
          }}
        >
          <Grid
            container
            style={{
              color: "#686963",
              fontSize: "30px",
              fontWeight: 600,
              marginTop: "4vh",
              justifyContent: "center",
            }}
          >
            Cause
          </Grid>
          <Grid
            container
            style={{
              color: "#686963",
              fontSize: "20px",
              padding: "6vh",
              textAlign: "center",
            }}
          >
            Lung cancer is one of the cancers caused by tobacco smoking, and
            second-hand smoke exposure increases lung cancer risk in never
            smokers
          </Grid>
        </Grid>

        <Grid
          container
          style={{
            alignItems: "center",
          }}
        >
          <img
            src={require("./Assets/Prevention.png")}
            alt="not found"
            style={{ width: "100%", height: "35vh" }}
          />
        </Grid>

        <Grid
          container
          style={{
            alignItems: "center",
          }}
        >
          <Grid
            container
            style={{
              color: "#686963",
              fontSize: "30px",
              fontWeight: 600,
              marginTop: "4vh",
              justifyContent: "center",
            }}
          >
            Prevention
          </Grid>
          <Grid
            container
            style={{
              color: "#686963",
              fontSize: "20px",
              padding: "6vh",
              textAlign: "center",
            }}
          >
            This app offers you all the right information and tools to track
            your progress of quiting smoking.
          </Grid>
        </Grid>

        <Grid
          container
          style={{
            alignItems: "center",
            backgroundColor: "#8AA29E",
          }}
        >
          <Grid
            container
            style={{
              color: "white",
              fontSize: "20px",
              padding: "6vh",
              textAlign: "center",
            }}
          >
            If you need any additional help please visit [Name of cancer support
            group/NGO] or [Name of local cancer clinic].
          </Grid>
          <Grid
            container
            style={{
              color: "white",
              fontSize: "20px",
              padding: "4vh",
              paddingTop: "2vh",
              textAlign: "center",
              justifyContent: "center",
            }}
          >
            About
          </Grid>
          <Grid
            container
            style={{
              color: "white",
              fontSize: "20px",
              padding: "6vh",
              paddingTop: "0px",
              justifyContent: "center",
              textAlign: "center",
            }}
          >
             2022 AppOwner
          </Grid>
        </Grid>
      </Grid>
    );
  }
}
